-color:#fff;border-radius:12px;box-shadow:0 6px 20px rgba(0,0,0,.1);margin-top:30px;padding:30px;transition:transform .3s ease}.add-article-form h3[data-v-863c7a10]{color:#333;font-size:24px;margin-bottom:20px}.add-article-form input[data-v-863c7a10],.add-article-form textarea[data-v-863c7a10]{border:1px solid #ccc;border-radius:6px;color:#333;font-size:16px;margin-bottom:20px;padding:12px;width:100%}.add-article-form button[data-v-863c7a10]{background-color:#3498db;border:none;border-radius:6px;color:#fff;cursor:pointer;font-size:18px;padding:12px 30px;transition:background-color .3s}.add-article-form button[data-v-863c7a10]:hover{background-color:#2980b9}.cancel-btn[data-v-863c7a10]{background-color:#e74c3c;border:none;border-radius:6px;color:#fff;cursor:pointer;font-size:18px;padding:12px 30px;transition:background-color .3s}.cancel-btn[data-v-863c7a10]:hover{background-color:#c0392b}", "",{"version":3,"sources":["webpack://./resources/js/components/veille/VeilleTechnologique.vue"],"names":[],"mappings":"AAwFA,6CAEA,wBAAA,CAIA,kBAAA,CACA,qCAAA,CANA,4BAAA,CAGA,aAAA,CADA,gBAAA,CAEA,YAGA,CAGA,yBAGA,wBAAA,CAGA,kBAAA,CAFA,UAAA,CAFA,kBAAA,CAGA,YAAA,CAJA,iBAMA,CAEA,4BACA,cAAA,CACA,eAAA,CACA,kBACA,CAEA,2BACA,cACA,CAGA,2BACA,YAAA,CAEA,QAAA,CADA,yDAEA,CAGA,0BACA,qBAAA,CAEA,kBAAA,CACA,oCAAA,CAFA,YAAA,CAGA,iDACA,CAEA,gCAEA,oCAAA,CADA,0BAEA,CAEA,6BAGA,UAAA,CAFA,cAAA,CACA,kBAEA,CAEA,4BAEA,UAAA,CADA,cAAA,CAEA,eACA,CAEA,wCAEA,UAAA,CADA,cAAA,CAEA,eACA,CAGA,4BAIA,wBAAA,CAGA,iBAAA,CAFA,UAAA,CAJA,oBAAA,CACA,eAAA,CACA,iBAAA,CAGA,oBAAA,CAEA,+BACA,CAEA,kCACA,wBACA,CAGA,6BACA,wBAAA,CAGA,WAAA,CACA,iBAAA,CAHA,UAAA,CAIA,cAAA,CACA,eAAA,CAJA,gBAAA,CAKA,+BACA,CAEA,mCACA,wBACA,CAGA,kCACA,YAAA,CACA,sBAAA,CACA,eACA,CAEA,yCACA,wBAAA,CAGA,WAAA,CACA,iBAAA,CAIA,qCAAA,CAPA,UAAA,CAKA,cAAA,CAJA,cAAA,CAGA,iBAAA,CAEA,+BAEA,CAEA,+CACA,wBACA,CAGA,mCACA,qBAAA,CAEA,kBAAA,CACA,oCAAA,CACA,eAAA,CAHA,YAAA,CAIA,6BACA,CAEA,sCAGA,UAAA,CAFA,cAAA,CACA,kBAEA,CAEA,qFAKA,qBAAA,CACA,iBAAA,CAEA,UAAA,CADA,cAAA,CAHA,kBAAA,CADA,YAAA,CADA,UAOA,CAEA,0CACA,wBAAA,CAIA,WAAA,CACA,iBAAA,CAJA,UAAA,CAKA,cAAA,CAJA,cAAA,CACA,iBAAA,CAIA,+BACA,CAEA,gDACA,wBACA,CAGA,6BACA,wBAAA,CAIA,WAAA,CACA,iBAAA,CAJA,UAAA,CAKA,cAAA,CAJA,cAAA,CACA,iBAAA,CAIA,+BACA,CAEA,mCACA,wBACA","sourcesContent":["<template>\n    <div class=\"technology-watch-container\">\n        <div class=\"header\">\n            <h1>Veille Technologique</h1>\n            <p>Suivez les dernières tendances et actualités en informatique</p>\n        </div>\n\n        <div class=\"articles\">\n            <div v-for=\"(article, index) in sortedArticles\" :key=\"article.title\" class=\"article\">\n                <h2>{{ article.title }}</h2>\n                <p>{{ article.description }}</p>\n                <p class=\"date-created\">Publié le : {{ formatDate(article.createdAt) }}</p>\n                <a :href=\"article.url\" target=\"_blank\" class=\"read-more\">Lire plus</a>\n                <!-- Bouton de suppression -->\n                <button @click=\"deleteArticle(index)\" class=\"delete-btn\">Supprimer</button>\n            </div>\n        </div>\n\n        <!-- Bouton '+' affiché après tous les articles -->\n        <div v-if=\"!isAddingArticle\" class=\"add-article-btn\">\n            <button @click=\"toggleAddArticle\">+</button>\n        </div>\n\n        <!-- Formulaire d'ajout d'article -->\n        <div v-if=\"isAddingArticle\" class=\"d-flex justify-content-start add-article-form\">\n            <h3>Ajouter un nouvel article</h3>\n            <form @submit.prevent=\"addArticle\">\n                <input v-model=\"newArticle.title\" type=\"text\" placeholder=\"Titre de l'article\" required />\n                <input v-model=\"newArticle.url\" type=\"url\" placeholder=\"URL de l'article\" required />\n                <textarea v-model=\"newArticle.description\" placeholder=\"Description de l'article\" required></textarea>\n                <button type=\"submit\">Ajouter</button>\n                <button @click=\"cancelAddArticle\" class=\"cancel-btn\">Annuler</button>\n            </form>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    data() {\n        return {\n            articles: [],\n            isAddingArticle: false,\n            newArticle: {\n                title: \"\",\n                url: \"\",\n                description: \"\",\n                createdAt: new Date()\n            }\n        }\n    },\n    computed: {\n        sortedArticles() {\n            return this.articles.sort((a, b) => b.createdAt - a.createdAt);\n        }\n    },\n    methods: {\n        toggleAddArticle() {\n            this.isAddingArticle = !this.isAddingArticle;\n        },\n        addArticle() {\n            if (this.newArticle.title && this.newArticle.url && this.newArticle.description) {\n                this.articles.push({ ...this.newArticle });\n                this.newArticle.title = \"\";\n                this.newArticle.url = \"\";\n                this.newArticle.description = \"\";\n                this.newArticle.createdAt = new Date();\n                this.isAddingArticle = false;\n            }\n        },\n        cancelAddArticle() {\n            this.isAddingArticle = false;\n            this.newArticle.title = \"\";\n            this.newArticle.url = \"\";\n            this.newArticle.description = \"\";\n        },\n        deleteArticle(index) {\n            this.articles.splice(index, 1);\n        },\n        formatDate(date) {\n            return `${date.toLocaleDateString('fr-FR')} à ${date.toLocaleTimeString('fr-FR')}`;\n        }\n    }\n}\n</script>\n\n<style scoped>\n/* Container principal */\n.technology-watch-container {\n    font-family: 'Arial', sans-serif;\n    background-color: #f7f7f7;\n    max-width: 1100px;\n    margin: 0 auto;\n    padding: 40px;\n    border-radius: 12px;\n    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);\n}\n\n/* En-tête */\n.header {\n    text-align: center;\n    margin-bottom: 40px;\n    background-color: #2c3e50;\n    color: #fff;\n    padding: 30px;\n    border-radius: 12px;\n}\n\n.header h1 {\n    font-size: 40px;\n    font-weight: 700;\n    margin-bottom: 10px;\n}\n\n.header p {\n    font-size: 20px;\n}\n\n/* Liste des articles */\n.articles {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n    gap: 30px;\n}\n\n/* Article individuel */\n.article {\n    background-color: #fff;\n    padding: 25px;\n    border-radius: 12px;\n    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);\n    transition: transform 0.3s ease, box-shadow 0.3s ease;\n}\n\n.article:hover {\n    transform: translateY(-5px);\n    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1);\n}\n\n.article h2 {\n    font-size: 28px;\n    margin-bottom: 15px;\n    color: #333;\n}\n\n.article p {\n    font-size: 16px;\n    color: #666;\n    line-height: 1.6;\n}\n\n.article .date-created {\n    font-size: 14px;\n    color: #888;\n    margin-top: 10px;\n}\n\n/* Lien \"Lire plus\" */\n.read-more {\n    display: inline-block;\n    margin-top: 20px;\n    padding: 12px 25px;\n    background-color: #3498db;\n    color: white;\n    text-decoration: none;\n    border-radius: 6px;\n    transition: background-color 0.3s;\n}\n\n.read-more:hover {\n    background-color: #2980b9;\n}\n\n/* Bouton de suppression */\n.delete-btn {\n    background-color: #e74c3c;\n    color: white;\n    padding: 8px 16px;\n    border: none;\n    border-radius: 6px;\n    cursor: pointer;\n    margin-top: 10px;\n    transition: background-color 0.3s;\n}\n\n.delete-btn:hover {\n    background-color: #c0392b;\n}\n\n/* Bouton d'ajout d'article */\n.add-article-btn {\n    display: flex;\n    justify-content: center;\n    margin-top: 30px;\n}\n\n.add-article-btn button {\n    background-color: #2ecc71;\n    color: white;\n    font-size: 40px;\n    border: none;\n    border-radius: 50%;\n    padding: 15px 20px;\n    cursor: pointer;\n    transition: background-color 0.3s;\n    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);\n}\n\n.add-article-btn button:hover {\n    background-color: #27ae60;\n}\n\n/* Formulaire d'ajout d'article */\n.add-article-form {\n    background-color: #fff;\n    padding: 30px;\n    border-radius: 12px;\n    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);\n    margin-top: 30px;\n    transition: transform 0.3s ease;\n}\n\n.add-article-form h3 {\n    font-size: 24px;\n    margin-bottom: 20px;\n    color: #333;\n}\n\n.add-article-form input,\n.add-article-form textarea {\n    width: 100%;\n    padding: 12px;\n    margin-bottom: 20px;\n    border: 1px solid #ccc;\n    border-radius: 6px;\n    font-size: 16px;\n    color: #333;\n}\n\n.add-article-form button {\n    background-color: #3498db;\n    color: white;\n    font-size: 18px;\n    padding: 12px 30px;\n    border: none;\n    border-radius: 6px;\n    cursor: pointer;\n    transition: background-color 0.3s;\n}\n\n.add-article-form button:hover {\n    background-color: #2980b9;\n}\n\n/* Bouton d'annulation */\n.cancel-btn {\n    background-color: #e74c3c;\n    color: white;\n    font-size: 18px;\n    padding: 12px 30px;\n    border: none;\n    border-radius: 6px;\n    cursor: pointer;\n    transition: background-color 0.3s;\n}\n\n.cancel-btn:hover {\n    background-color: #c0392b;\n}\n</style>\n"],"sourceRoot":""}]);
